.widget {
	margin-bottom: 40px;
	padding: 20px;
	background: white;

	&__title {
		position: relative;
		margin-bottom: 20px;
		padding-bottom: 5px;
		text-transform: uppercase;

		&::after {
			content: '';
			position: absolute;
			bottom: 0;
			left: 0;
			width: 100%;
			height: 2px;
			background: $purple;
		}
	}
}

.widget-gallery {
	.gallery-list {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		grid-gap: 10px;

		a {
			display: block;
			height: 78px;
			overflow: hidden;
		}
	}
}
